@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;900&display=swap);
*::after,
*::before,
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  transition: all 150ms;
  -webkit-transition: all 150ms;
  -moz-transition: all 150ms;
  -ms-transition: all 150ms;
  -o-transition: all 150ms;
}

body {
  font-size: 16px;
  font-family: "Inter", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex !important;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  height: 100vh;
  width: 100vw;
  padding: 3rem;
  overflow: hidden;
  background-color: #e2e8f0;
}

.app-nav a:not(.active-link) {
  color: #64748b;
}

.app-nav a {
  font-size: 14px;
  margin-right: 1rem;
  text-decoration: none;
}

.active-link {
  color: #1a202e;
  border-bottom: solid 2px #1a202e;
  /* font-weight: 700; */
}

.app-nav {
  min-width: 34%;
  padding: 1rem 0 4rem;
  display: flex;
  justify-content: flex-start;
}

.item {
  padding: 0.7rem 0;
  border-bottom: solid 0.5px;
  border-color: rgba(241, 245, 249, 1);
}

.item:last-child {
  border-bottom: none;
}

.item .svg {
  opacity: 0;
  cursor: pointer;
}

.item:hover .svg {
  opacity: 0.5;
}

.item:hover .svg:hover {
  opacity: 1;
}

.relative {
  position: relative;
}

.relative .svg {
  opacity: 1;
  cursor: pointer;
  position: absolute;
  right: 0.7rem;
}

.relative .svg:hover {
  opacity: 0.5;
}

.relative .fullinput {
  font-size: 16px;
  width: 100%;
  padding: 0.7rem 0.4rem;
  border: none;
}

.fullinput::-webkit-input-placeholder {
  font-size: 16px;
}

.fullinput:-ms-input-placeholder {
  font-size: 16px;
}

.fullinput::placeholder {
  font-size: 16px;
}

.flexy {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.sb {
  margin-bottom: 2rem;
}

.add {
  width: 32px;
  height: 32px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 3px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
}
.add:hover {
  box-shadow: 0px 0px 0px 0.5px #64748b;
}

.todo-app {
  background-color: #fff;
  border-radius: 5px;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  -ms-border-radius: 5px;
  -o-border-radius: 5px;
  padding: 3rem;
  min-width: 34%;
  box-shadow: 0px 5px 14px #64748b1c;
}

